import React from "react";
import { Link } from 'react-router-dom';
import { AppContext } from "../common/AppContext";
import AutoBindComponent from "../common/AutobindComponent";
import LoginPageHelper from "./LoginPageHelper";

import { Container } from "semantic-ui-react";

export class LoginPage extends AutoBindComponent {
    constructor(props, context) {
        super(props, context);

        this.state = {
            username: "",
            password: "",
            submitted: false,
            authenticationError: ""
        };
    }

    componentDidMount(){
        this.pageHelper = new LoginPageHelper(this);
        this.pageHelper.logout();
    }

    handleOnChange(e){
        const { name, value } = e.target;
        this.setState({ [name]: value });
    }

    handleOnSubmit(e){
        e.preventDefault();
        this.setState({ submitted: true });

        const { username, password } = this.state;

        if (username && password) {
            this.pageHelper.login(username, password);
        }
    }

    render() {
        const { loggingIn } = this.props.appContext;
        const { username, password, submitted } = this.state;

        return (
            <Container text>
                <h2>Login</h2>
                <form name="form" onSubmit={this.handleOnSubmit}>
                    <div className={'form-group' + (submitted && !username ? ' has-error' : '')}>
                        <label htmlFor="username">Username</label>
                        <input type="text" className="form-control" name="username" value={username} onChange={this.handleOnChange} />
                        {submitted && !username &&
                            <div className="help-block">Username is required</div>
                        }
                    </div>
                    <div className={'form-group' + (submitted && !password ? ' has-error' : '')}>
                        <label htmlFor="password">Password</label>
                        <input type="password" className="form-control" name="password" value={password} onChange={this.handleOnChange} />
                        {submitted && !password &&
                            <div className="help-block">Password is required</div>
                        }
                    </div>
                    <div className="form-group">
                        <button className="btn btn-primary">Login</button>
                        {loggingIn &&
                            <img src="data:image/gif;base64," />
                        }
                        <Link to="/register" className="btn btn-link">Register</Link>
                    </div>
                    {this.state.authenticationError === "" ? null :
                    <div>
                        <span style={{color: "red"}}>{this.state.authenticationError}</span>
                    </div>}
                </form>
            </Container>
        );
    }
}

export default React.forwardRef((props, ref) => (
    <AppContext.Consumer>
        {appContext => <LoginPage {...props} appContext={appContext} ref={ref} />}
    </AppContext.Consumer>
));


